* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  font-family: 'Arial', sans-serif;
  display: flex;
  min-height: 100vh;
  background-color: #f0f2f5;
}

/* Sidebar */
.sidebar {
  background-color: #1e2a44;
  color: white;
  width: 20vw; /* Escala con el ancho de la pantalla (mínimo 200px más abajo) */
  min-width: 200px;
  padding: 15px;
  transition: width 0.3s ease;
}

.menu-toggle {
  display: none;
  font-size: 24px;
  cursor: pointer;
  padding: 10px;
}

.menu h1 {
  font-size: 22px;
  margin-bottom: 20px;
  text-align: center;
}

.menu button {
  display: block;
  width: 100%;
  background-color: #34495e;
  color: white;
  border: none;
  padding: 12px;
  margin: 5px 0;
  cursor: pointer;
  border-radius: 5px;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.menu button:hover {
  background-color: #3498db;
}

/* Contenido */
.contenido {
  flex-grow: 1;
  padding: 15px;
  overflow-y: auto;
}

.seccion h2 {
  color: #1e2a44;
  font-size: 24px;
  margin-bottom: 10px;
}

.seccion p {
  color: #666;
  margin-bottom: 15px;
}

/* Tablas */
.tabla-contenedor {
  overflow-x: auto;
  margin: 10px 0;
}

table {
  width: 100%;
  border-collapse: collapse;
  background-color: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

th,
td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
  font-size: 14px;
}

th {
  background-color: #3498db;
  color: white;
}

tr:nth-child(even) {
  background-color: #f9f9f9;
}

/* Botones */
button {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 8px 12px;
  cursor: pointer;
  border-radius: 5px;
  font-size: 14px;
  transition: background-color 0.3s ease;
  margin-right: 8px;
}

button:hover {
  background-color: #c0392b;
}

/* Formularios */
form {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 20px;
}

input,
select {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 14px;
  flex: 1;
  min-width: 120px;
}

.form-turno {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
}

/* Notificaciones */
.notificacion {
  position: fixed;
  top: 10px;
  right: 10px;
  padding: 10px 15px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  color: white;
  font-size: 14px;
}

.notificacion.success {
  background-color: #27ae60; /* Verde para éxito */
}

.notificacion.error {
  background-color: #e74c3c; /* Rojo para error */
}

/* Secciones específicas */
.stats,
.entregas,
.alertas,
.accesos {
  margin: 15px 0;
}

.stats p {
  margin: 5px 0;
  font-size: 14px;
  color: #333;
}

.alertas ul {
  list-style: none;
  padding: 0;
}

.alertas li {
  margin: 8px 0;
  font-size: 14px;
  color: #666;
}

.accesos button {
  margin: 0 8px 8px 0;
}

td button {
  margin: 0 8px 8px 0;
}

form button {
  margin: 0 8px 8px 0;
}

/* Login */
.login {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 100vh;
  width: 100%;
  background: linear-gradient(135deg, #1e2a44 0%, #3498db 100%);
  padding: 40px;
}

.login::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('https://www.transparenttextures.com/patterns/asfalt-dark.png') repeat;
  opacity: 0.1;
  z-index: 0;
}

.login-container {
  background-color: rgba(255, 255, 255, 0.95);
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
  width: 100%;
  max-width: 450px;
  text-align: center;
  position: relative;
  z-index: 1;
  animation: fadeIn 0.5s ease-in-out;
}

.login-text {
  color: white;
  width: 50%;
  max-width: 600px;
  text-align: left;
  padding: 20px;
  z-index: 1;
  animation: slideIn 1s ease-in-out;
}

.login-text h1 {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 20px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.login-text p {
  font-size: 20px;
  line-height: 1.6;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.2);
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes slideIn {
  from { opacity: 0; transform: translateX(50px); }
  to { opacity: 1; transform: translateX(0); }
}

.login h2 {
  color: #1e2a44;
  font-size: 32px;
  margin-bottom: 25px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.login form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.login input {
  padding: 14px;
  font-size: 16px;
  border: 2px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.login input:focus {
  border-color: #3498db;
  box-shadow: 0 0 8px rgba(52, 152, 219, 0.5);
  outline: none;
}

.login button {
  padding: 14px;
  font-size: 18px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-right: 0;
}

.login button:hover {
  background-color: #2980b9;
  transform: scale(1.05);
}

/* Breakpoints para iPhone 8 (375px) y menores */
@media (max-width: 375px) {
  .App {
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
    padding: 0;
    min-width: unset; /* Eliminar min-width para pantallas pequeñas */
  }

  .menu-toggle {
    display: block;
  }

  .menu {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease;
  }

  .menu.abierto {
    max-height: 300px;
  }

  .menu h1 {
    display: none;
  }

  .menu button {
    padding: 12px;
    font-size: 14px;
  }

  .contenido {
    padding: 10px;
  }

  .seccion h2 {
    font-size: 20px;
  }

  .seccion p {
    font-size: 12px;
  }

  th, td {
    padding: 6px;
    font-size: 11px;
  }

  button {
    padding: 6px 8px;
    font-size: 12px;
    margin-right: 4px;
  }

  input, select {
    padding: 6px;
    font-size: 12px;
    min-width: 100px;
  }

  .login {
    padding: 10px;
    gap: 20px;
  }

  .login-container {
    padding: 20px;
    max-width: 90%;
  }

  .login h2 {
    font-size: 24px;
  }

  .login-text h1 {
    font-size: 26px;
  }

  .login-text p {
    font-size: 14px;
  }

  .login input {
    padding: 10px;
    font-size: 14px;
  }

  .login button {
    padding: 10px;
    font-size: 14px;
  }
}

